import './scss/styles.scss';
import { AuctionAPI } from "./components/AuctionAPI";
import { API_URL, CDN_URL } from "./utils/constants";
import { EventEmitter } from "./components/base/events";
import { AppState } from './components/LotItem';
import { cloneTemplate, ensureElement } from './utils/utils';
import { AuctionItem, CatalogItem } from './components/Card';
import { Page } from './components/Page';
import { Modal } from './components/common/Modal';
const events = new EventEmitter();
const api = new AuctionAPI(CDN_URL, API_URL);
// Чтобы мониторить все события, для отладки
events.onAll(({ eventName, data }) => {
    console.log(eventName, data);
});
// Все шаблоны
const cardCatalogTemplate = ensureElement('#card');
const cardPreviewTemplate = ensureElement('#preview');
// Модель данных приложения
const appData = new AppState({}, events);
// Глобальные контейнеры
const page = new Page(document.body, events);
const modal = new Modal(ensureElement('#modal-container'), events);
// Переиспользуемые части интерфейса
// Дальше идет бизнес-логика
// Поймали событие, сделали что нужно
events.on('items:changed', () => {
    page.catalog = appData.catalog.map(item => {
        const card = new CatalogItem(cloneTemplate(cardCatalogTemplate), {
            onClick: () => events.emit('card:select', item)
        });
        return card.render({
            title: item.title,
            image: item.image,
            description: item.about,
            status: {
                status: item.status,
                label: item.statusLabel
            },
        });
    });
    // page.counter = appData.getClosedLots().length;
});
// Открыть лот
events.on('card:select', (item) => {
    appData.setPreview(item);
});
// Изменен открытый выбранный лот
events.on('preview:changed', (item) => {
    const showItem = (item) => {
        const card = new AuctionItem(cloneTemplate(cardPreviewTemplate));
        modal.render({
            content: card.render({
                title: item.title,
                image: item.image,
                description: item.description.split("\n"),
            })
        });
    };
    if (item) {
        api.getLotItem(item.id)
            .then((result) => {
            item.description = result.description;
            item.history = result.history;
            showItem(item);
        })
            .catch((err) => {
            console.error(err);
        });
    }
    else {
        modal.close();
    }
});
// Блокируем прокрутку страницы если открыта модалка
events.on('modal:open', () => {
    page.locked = true;
});
// ... и разблокируем
events.on('modal:close', () => {
    page.locked = false;
});
// Получаем лоты с сервера
api.getLotList()
    .then(appData.setCatalog.bind(appData))
    .catch(err => {
    console.error(err);
});
